export const randomInteger = (min: number, max: number) => {
  return Math.round(min - 0.5 + Math.random() * (max - min + 1));
};
export const isEven = (n: number) => {
  return n % 2 === 0;
};

export const numWithModClassic = (current: number, max: number) => {
  // —Ü–µ–ª—å –æ—Å—Ç–∞–≤–∏—Ç—å —á–∏—Å–ª–æ —Å–ø–∏—á–µ–∫, —Å—Ä–∞–≤–Ω–∏–º–æ–µ —Å 0 –∏–ª–∏ 1 –ø–æ –º–æ–¥—É–ª—é 4.
  let modZero = 0;
  let modDelimetr = 4;

  for (let num = 1; num <= max; num++) {
    if (num % 2 !== 0) {
      if ((current - num) % modDelimetr === 1) {
        return num;
      }
      if ((current - num) % modDelimetr === 0) {
        modZero = num;
      }
    }
  }
  return modZero;
};

export const numWithModCustom = (
  current: number,
  max: number,
  customStart: boolean = false,
  botScore: number,
) => {
  // –¥–ª—è –Ω–µ—á–µ—Ç–Ω–æ–≥–æ max, —Ü–µ–ª—å –æ—Å—Ç–∞–≤–∏—Ç—å —Ç–µ–∫—É—â–µ–µ —á–∏—Å–ª–æ —Å–ø–∏—á–µ–∫, —Å—Ä–∞–≤–Ω–∏–º–æ–µ —Å 0 –∏–ª–∏ 1 –ø–æ –º–æ–¥—É–ª—é 4
  // –¥–ª—è —á–µ—Ç–Ω–æ–≥–æ max, —Å—Ä–∞–≤–Ω–∏–º–æ–µ —Å 0 –∏–ª–∏ 1 –ø–æ –º–æ–¥—É–ª—é 6 –∏–ª–∏ —á–∏—Å–ª–æ, —Å—Ä–∞–≤–Ω–∏–º–æ–µ —Å 5 –ø–æ –º–æ–¥—É–ª—é 6 –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç —á–µ—Ç–Ω–æ—Å—Ç–∏ botScore

  let modSecond = 1;
  let modDelimetr = max % 2 !== 0 ? 4 : 6;

  if (modDelimetr === 4)
    for (let num = 1; num <= max; num++) {
      if (num % 2 !== 0 || customStart) {
        if ((current - num) % modDelimetr === 0) {
          return num;
        }
        if ((current - num) % modDelimetr === 1) {
          modSecond = num;
        }
      }
    }
  else {
    for (let num = 1; num <= max; num++) {
      if (botScore % 2 === 0 || botScore === 0) {
        if ((current - num) % modDelimetr === 1) {
          return num;
        }
        if ((current - num) % modDelimetr === 0) {
          modSecond = num;
        }
      } else {
        if ((current - num) % modDelimetr === 5) {
          return num;
        }
      }
    }
  }

  return modSecond;
};
